import React, { Component } from 'react';
import { Space, List, Toast } from 'antd-mobile'
import { UserCircleOutline, UnorderedListOutline, PhoneFill, PayCircleOutline, LinkOutline } from 'antd-mobile-icons'

import { Header } from '../../../component/mobile';

class Ucenter extends Component {
    constructor(props) {
        super(props);
        const { isBind } = props.location.params || {};
        this.state = {
            isBind: !!isBind || true
        }
    }

    toPay = () => {
        const { isBind } = this.state;
        if (isBind) {
            Toast.show({
                content: '会跳转充值页面',
            });
            return;
        }
        this.props.history.push('/h5/bind')
    }

    toBind = () => {
        const { isBind } = this.state;
        if (isBind) {
            Toast.show({
                content: '解除绑定',
                afterClose: () => {
                    this.setState({
                        isBind: false
                    });
                }
            });
            return;
        }
        this.props.history.push('/h5/bind')
    }

    render() {
        const { isBind } = this.state;
        return (
            <>
                <Header title='个人中心' />
                <Space justify='between' align='center' style={{ padding: 20 }}>
                    <UserCircleOutline fontSize={24} />
                    {isBind ? '李建国' : '未绑定患者'}
                </Space>

                <List>
                    <List.Item prefix={<UnorderedListOutline />} clickable={false}>
                        身份证：{isBind ? '10000002323002302030' : '未绑定患者'}
                    </List.Item>
                    <List.Item prefix={<PhoneFill />} clickable={false}>
                        手机号： {isBind ? '15788889999' : '未绑定患者'}
                    </List.Item>
                    <List.Item prefix={<PayCircleOutline />} onClick={this.toPay}>
                        <Space justify='between' style={{ width: '100%' }}>
                            <span>账户余额： {isBind ? '2519.12元' : '未绑定患者'}</span>
                            <span style={{ coloe: 'green', fontSize: 14 }}></span>
                        </Space>
                    </List.Item>
                    <List.Item prefix={<LinkOutline />} onClick={this.toBind}>
                        <Space justify='between' style={{ width: '100%' }}>
                            <span>微信账号</span>
                            <span style={{ coloe: 'green', fontSize: 14 }}>{isBind ? '解除绑定' : '绑定微信'}</span>
                        </Space>
                    </List.Item>
                </List>
            </>
        );
    }
}

export default Ucenter;